// useUserInfo.tsx
import { useState, useEffect } from "react";
import { debugLog } from "./debugLog";

/**
 * useUserInfo - custom hook that reads current Outlook item and returns useful fields.
 * Works inside an Outlook add-in (Office.context must be available).
 */
export function useUserInfo() {
    const [userDisplayName, setUserDisplayName] = useState<string>("");
    const [userEmailAddress, setUserEmailAddress] = useState<string>("");
    const [senderName, setSenderName] = useState<string>("");
    const [senderEmail, setSenderEmail] = useState<string>("");
    const [userEmail, setUserEmail] = useState<string>("");
    const [userName, setUserName] = useState<string>("");
    const [ccEmails, setCcEmails] = useState<string[]>([]);
    const [toEmails, setToEmails] = useState<string[]>([]);
    const [mailBody, setMailBody] = useState<string>("");
    const [attachments, setAttachments] = useState<
        { base64: string; contentType: string; name: string }[]
    >([]);
    const [attachmentNames, setAttachmentNames] = useState<string[]>([]);
    const [dateTime, setDateTime] = useState<string>("");
    const [message_id, setMessage_id] = useState<string>("");
    const [thread_id, setThread_id] = useState<string>("");
    const [subject, setSubject] = useState<string>("");

    useEffect(() => {
        // If Office is unavailable, bail out.
        if (typeof window === "undefined" || !(window as any).Office || !(window as any).Office.context) {
            debugLog("useUserInfo:", "Office.context not available");
            return;
        }

        const item: any = (window as any).Office.context.mailbox.item;
        const profile = (window as any).Office.context.mailbox.userProfile;

        // Basic user profile
        setUserDisplayName(profile?.displayName || "");
        setUserEmailAddress(profile?.emailAddress || "");
        setUserEmail(profile?.emailAddress || "");
        setUserName(profile?.displayName || "");

        // Message / ids
        try {
            if (item?.itemId) {
                setMessage_id(encodeURIComponent(item.itemId));
            }
            if (item?.conversationId) {
                setThread_id(encodeURIComponent(item.conversationId));
            }
        } catch (err) {
            debugLog("useUserInfo: error encoding ids", err);
        }

        // Sender
        setSenderName(item?.sender?.displayName || "");
        setSenderEmail(item?.sender?.emailAddress || "");

        // Recipients: cc
        try {
            const rawCc = item?.cc || [];
            const ccArr: string[] = [];
            for (let i = 0; i < rawCc.length; i++) {
                ccArr.push(rawCc[i]?.emailAddress || "");
            }
            setCcEmails(ccArr);
        } catch (err) {
            debugLog("useUserInfo: error reading cc", err);
            setCcEmails([]);
        }

        // Recipients: to
        try {
            const rawTo = item?.to || [];
            const toArr: string[] = [];
            rawTo.forEach((r: any) => {
                toArr.push(r?.emailAddress || "");
            });
            setToEmails(toArr);
        } catch (err) {
            debugLog("useUserInfo: error reading to", err);
            setToEmails([]);
        }

        // Subject
        setSubject(item?.subject || "");

        // Date/time created -> ISO string
        try {
            const created = item?.dateTimeCreated;
            if (created) {
                const dt = new Date(created);
                setDateTime(dt.toISOString());
            } else {
                setDateTime("");
            }
        } catch (err) {
            debugLog("useUserInfo: date parse error", err);
            setDateTime("");
        }

        // Body (HTML -> plain-ish text)
        try {
            item.body.getAsync((window as any).Office.CoercionType.Html, function (result: any) {
                if (result.status === (window as any).Office.AsyncResultStatus.Succeeded) {
                    let html = result.value || "";

                    // Convert some tags -> newlines and strip tags
                    const formatted = html
                        .replace(/&nbsp;/g, " ")
                        .replace(/<br\s*\/?>/gi, "\n")
                        .replace(/<\/p>|<\/div>/gi, "\n")
                        .replace(/<[^>]+>/g, "")
                        .replace(/\s+$/g, "");

                    setMailBody(formatted);
                } else {
                    debugLog("useUserInfo: failed to get body", result.error?.message || result.error);
                    setMailBody("");
                }
            });
        } catch (err) {
            debugLog("useUserInfo: body.getAsync threw", err);
            setMailBody("");
        }

        // Attachments: we will collect names and fetch content via getAttachmentContentAsync
        const rawAttachments = item?.attachments || [];
        if (!rawAttachments || rawAttachments.length === 0) {
            setAttachmentNames([]);
            setAttachments([]);
            return;
        }

        const names: string[] = [];
        rawAttachments.forEach((att: any) => names.push(att?.name || ""));
        setAttachmentNames(names);

        // Promisify getAttachmentContentAsync
        const getAttachmentContent = (attachment: any) =>
            new Promise<{ base64: string; contentType: string; name: string }>((resolve, reject) => {
                try {
                    item.getAttachmentContentAsync(attachment.id, function (result: any) {
                        if (result.status === (window as any).Office.AsyncResultStatus.Succeeded) {
                            // result.value.content is base64 string for file attachments (for item attachments)
                            resolve({
                                base64: result.value?.content || "",
                                contentType: attachment?.contentType || "application/octet-stream",
                                name: attachment?.name || "attachment"
                            });
                        } else {
                            debugLog("useUserInfo: getAttachmentContentAsync failed", result.error);
                            // resolve an empty entry so we don't block all attachments
                            resolve({
                                base64: "",
                                contentType: attachment?.contentType || "application/octet-stream",
                                name: attachment?.name || "attachment"
                            });
                        }
                    });
                } catch (err) {
                    debugLog("useUserInfo: getAttachmentContentAsync threw", err);
                    reject({
                        base64: "",
                        contentType: attachment?.contentType || "application/octet-stream",
                        name: attachment?.name || "attachment"
                    });
                }
            });

        // Kick off all attachments reads
        (async () => {
            try {
                const promises = rawAttachments.map((a: any) => getAttachmentContent(a));
                const results = await Promise.all(promises);
                // Filter out attachments with no base64 content (optional)
                const valid = results.filter(r => !!r.base64);
                // If none had base64, still return the name/empty base64 so UI can show names
                setAttachments(results);
            } catch (err) {
                debugLog("useUserInfo: attachments read error", err);
                setAttachments([]);
            }
        })();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return {
        userDisplayName,
        userEmailAddress,
        senderName,
        senderEmail,
        userEmail,
        userName,
        ccEmails,
        toEmails,
        mailBody,
        attachments,
        attachmentNames,
        dateTime,
        message_id,
        thread_id,
        subject
    };
}

export default useUserInfo;