// utilities/getOutlookBrowserLink.ts
export const getOutlookBrowserLink = (): string => {
    try {
        // This is the ONLY reliable way in Outlook Web Add-ins
        if ((window as any).Office) {
            const item = (window as any).Office.context.mailbox.item;
            if (item) {
                // Works in both read and compose mode
                return item.getRegressedItemUrl?.() ||
                    (window as any).Office.context.mailbox.convertToRestId?.(
                        item.itemId,
                        (window as any).Office.MailboxEnums.RestVersion.v2_0
                    )
                    ? `https://outlook.live.com/mail/0/inbox/id/${item.itemId}`
                    : "";
            }
        }

        // Fallback: try to extract from parent window (sometimes works)
        if (window.location !== window.parent.location) {
            const parentUrl = window.parent.location.href;
            if (parentUrl.includes("outlook.live.com") || parentUrl.includes("outlook.office.com")) {
                return parentUrl;
            }
        }

        return "";
    } catch (err) {
        console.error("Failed to get Outlook browser link:", err);
        return "";
    }
};