export const searchCase = async (
    query: string,
    callback: (error: Error | null, data: any | null) => void
): Promise<void> => {
    const formData = new FormData();
    formData.append("fileName", query);

    try {
        // Send to backend
        const response = await fetch(`https://optimo-backend.vercel.app/search-cases?q=${encodeURIComponent(query)}`, {
            method: "GET",
        });

        const data = await response.json();
        if (data.results.length >= 0) {
            console.log(data);
            callback(null, data);
        } else {
            callback(new Error(data.message), null);
        };
    } catch (error) {
        console.error("Error creating Notion page:", error);
        callback(error as Error, null);
    };
};