import React, { useState } from 'react';
import { Stack, Box, Typography, Button } from '@mui/material';
import UIText from '../../utilities/textResource';
import Loader from '../loader/loader';
import Toast from '../toast/Toast';
import useUserInfo from '../../utilities/userInfo';
import { EmailDetailsProps, ToastState } from '../../utilities/types';
import { formatDateTime } from '../../utilities/formatDateTime';
import { createNotionEntry } from '../../services/createEntry';
import { getOutlookBrowserLink } from '../../utilities/getOutlookBrowserLink';

const EmailDetails: React.FC<EmailDetailsProps> = ({ linkedCaseId }) => {
    const [isLoading, setIsLoading] = useState(false);
    const [toast, setToast] = useState<ToastState>(null);
    const { senderEmail, userEmail, subject, dateTime, message_id, attachments, mailBody } = useUserInfo();
    const browserLink = getOutlookBrowserLink();
    const formattedDateTime = formatDateTime(dateTime);

    const handleSendToOptimo = async () => {
        if (!subject || !message_id) {
            setToast({
                type: "error",
                message: "Missing email data. Cannot create Notion entry.",
                time: Date.now()
            });
            return;
        };

        setIsLoading(true);
        try {
            const base64ToFile = (
                att: { base64?: string; contentType?: string; name?: string }
            ): File | null => {
                if (!att || !att.base64 || att.base64.trim() === "") {
                    console.warn("Skipping attachment because it has no valid base64:", att);
                    return null;
                }

                try {
                    const binaryString = atob(att.base64);
                    const len = binaryString.length;
                    const bytes = new Uint8Array(len);
                    for (let i = 0; i < len; i++) bytes[i] = binaryString.charCodeAt(i);

                    const blob = new Blob([bytes], {
                        type: att.contentType || "application/octet-stream",
                    });

                    return new File(
                        [blob],
                        att.name || "attachment",
                        { type: att.contentType || "application/octet-stream" }
                    );
                } catch (error) {
                    console.warn("Invalid base64 detected. Skipping:", att, error);
                    return null;
                }
            };

            // Convert only valid attachments  filter out null
            const files: File[] = attachments
                .map(base64ToFile)
                .filter((f): f is File => f !== null);

            await createNotionEntry(subject, files, dateTime, browserLink, linkedCaseId, message_id, mailBody, (error, data) => {
                if (error) {
                    console.log("error", error);

                    setToast({
                        type: "error",
                        message: "Failed to create Notion entry. Please try again.",
                        time: Date.now()
                    });
                } else {
                    console.log("success", data);

                    setToast({
                        type: "success",
                        message: "Notion entry created successfully!",
                        time: Date.now()
                    });
                };
            });
        } catch (error) {
            console.error("Error sending to OPTIMO:", error);

            setToast({
                type: "error",
                message: "ailed to create Notion entry. Please try again.",
                time: Date.now()
            });
        } finally {
            setIsLoading(false);
        };
    };

    return (
        <>
            <Box sx={{ wordBreak: 'break-word', whiteSpace: 'normal' }}>
                <Typography sx={{ margin: "12px 0", fontWeight: 700 }}>
                    {UIText.emailDetails.title}
                </Typography>

                <Stack sx={{ flexDirection: "column" }}>
                    <Box sx={{ display: "flex", flexDirection: "row", margin: "4px 0px" }}>
                        <Typography sx={{ minWidth: 70 }}>{UIText.emailDetails.from}</Typography>
                        <Typography
                            sx={{
                                color: "#2872AB",
                                cursor: "pointer",
                                fontSize: "14px",
                                "&:hover": { textDecoration: "underline" }
                            }}
                        >
                            {senderEmail}
                        </Typography>
                    </Box>

                    <Box sx={{ display: "flex", flexDirection: "row", margin: "4px 0px" }}>
                        <Typography sx={{ minWidth: 70 }}>{UIText.emailDetails.to}</Typography>
                        <Typography
                            sx={{
                                color: "#2872AB",
                                cursor: "pointer",
                                fontSize: "14px",
                                "&:hover": { textDecoration: "underline" }
                            }}
                        >
                            {userEmail}
                        </Typography>
                    </Box>

                    <Box sx={{ display: "flex", flexDirection: "row", margin: "4px 0px" }}>
                        <Typography sx={{ minWidth: 70 }}>{UIText.emailDetails.subject}</Typography>
                        <Typography sx={{ fontSize: "14px" }}>{subject}</Typography>
                    </Box>

                    <Box sx={{ display: "flex", flexDirection: "row", margin: "4px 0px" }}>
                        <Typography sx={{ minWidth: 70 }}>{UIText.emailDetails.date}</Typography>
                        <Typography sx={{ fontSize: "14px" }}>{formattedDateTime}</Typography>
                    </Box>
                </Stack>

                <Stack sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    mt: 2
                }}>
                    <Button
                        variant="contained"
                        sx={{
                            textTransform: "capitalize",
                            width: "60%",
                            maxWidth: 600,
                            display: "flex",
                            alignItems: "center",
                            gap: 1
                        }}
                        onClick={handleSendToOptimo}
                        disabled={isLoading}
                    >
                        {isLoading && (
                            <Box sx={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                                <Loader loader={{ size: "16px" }} loaderOverlay={{}} />
                            </Box>
                        )}
                        {isLoading ? "Creating Notion entry..." : UIText.emailDetails.button}
                    </Button>
                </Stack>

                {toast?.type === 'info' && <Toast key={toast.time} info={toast.message} />}
                {toast?.type === 'error' && <Toast key={toast.time} error={toast.message} />}
                {toast?.type === 'success' && <Toast key={toast.time} success={toast.message} />}
            </Box>
        </>
    );
};

export default EmailDetails;