import React, { useState, useEffect } from 'react';
import { Box, List, ListItemButton, ListItemText, Paper, Stack, TextField, Typography } from '@mui/material';
import { Props, CaseResult, ToastState } from '../../utilities/types';
import Loader from '../loader/loader';
import Toast from '../toast/Toast';
import UIText from '../../utilities/textResource';
import { searchCase } from '../../services/search';

const Search: React.FC<Props> = ({ onSelect }) => {
    const [query, setQuery] = useState("");
    const [isLoading, setIsLoading] = useState(false);
    const [hideSearchResult, setHideSearchResult] = useState(false);
    const [results, setResults] = useState<CaseResult[]>([]);
    const [toast, setToast] = useState<ToastState>(null);
    const [selectedCase, setSelectedCase] = useState<CaseResult | null>(null);

    const handleSelect = (item: CaseResult) => {
        console.log("Selected case:", item);
        const title =
            item.raw?.[" "]?.title?.[0]?.plain_text ||
            item.raw?.[" "]?.title?.[0]?.text?.content ||
            item.title ||
            "(untitled)";
        setSelectedCase(item);
        setQuery(title);
        setResults([]);
        onSelect(item.id);
        setHideSearchResult(true);
    };

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const value = e.target.value;
        setQuery(value);
        setHideSearchResult(false);

        if (selectedCase) {
            setSelectedCase(null);
            onSelect(null);
        }
    };

    useEffect(() => {
        if (query.length < 2) {
            setResults([]);
            return;
        } else if (hideSearchResult !== false) {
            return;
        };

        setIsLoading(true);

        try {
            searchCase(query, (error, data) => {
                if (error) {
                    console.log("error", error);

                    setToast({
                        type: "error",
                        message: "Failed to search cases. Please try again.",
                        time: Date.now()
                    });

                    setIsLoading(false);
                } else {
                    console.log("data.results", data.results);
                    setResults(data?.results || []);
                    setIsLoading(false);
                };
            });
        } catch (error) {
            console.error("Error sending to OPTIMO:", error);

            setToast({
                type: "error",
                message: "Failed to search cases. Please try again.",
                time: Date.now()
            });

            setIsLoading(false);
        }
    }, [query]);

    return (
        <>
            <Stack sx={{ position: "relative", width: "100%", maxWidth: 600 }}>
                <TextField
                    variant="filled"
                    size="small"
                    placeholder="Search..."
                    label="Search client or case"
                    value={query}
                    onChange={handleInputChange}
                    sx={{ width: "100%" }}
                />

                {isLoading && (
                    <Box sx={{ display: "flex", mt: 1 }}>
                        <Loader loader={{ size: "16px" }} loaderOverlay={undefined} />
                        <Typography sx={{ pl: 1, pt: 0.5, color: "#959595" }}>
                            {UIText.search.loading}
                        </Typography>
                    </Box>
                )}

                {results.length > 0 && query.length >= 2 && !isLoading && hideSearchResult !== true && (
                    <Paper
                        elevation={3}
                        sx={{
                            position: "absolute",
                            top: "100%",
                            left: 0,
                            width: "100%",
                            zIndex: 20,
                            mt: 1,
                            maxHeight: 370,
                            overflowY: "auto",
                            borderRadius: 0.5
                        }}
                    >
                        <List sx={{ py: 0.5 }}>
                            {results.map((item) => {
                                const title =
                                    item.raw?.[" "]?.title?.[0]?.plain_text ||
                                    item.raw?.[" "]?.title?.[0]?.text?.content ||
                                    item.title ||
                                    "(untitled)";

                                return (
                                    <ListItemButton
                                        key={item.id}
                                        onClick={() => handleSelect(item)}
                                        sx={{
                                            borderBottom: "1px solid #eee",
                                            "&:hover": { backgroundColor: "#f5f5f5" },
                                            paddingY: 1
                                        }}
                                    >
                                        <ListItemText
                                            primary={title}
                                            sx={{
                                                overflow: "hidden",
                                                textOverflow: "ellipsis",
                                                whiteSpace: "nowrap",
                                                maxWidth: "100%"
                                            }}
                                        />
                                    </ListItemButton>
                                );
                            })}
                        </List>
                    </Paper>
                )}

                {!isLoading && query.length >= 2 && results.length === 0 && hideSearchResult !== true && (
                    <Typography sx={{ mt: 1, color: "secondary", fontSize: "14px", fontWeight: "semibold" }}>
                        No case found
                    </Typography>
                )}
            </Stack>

            {toast?.type === 'info' && <Toast key={toast.time} info={toast.message} />}
            {toast?.type === 'error' && <Toast key={toast.time} error={toast.message} />}
            {toast?.type === 'success' && <Toast key={toast.time} success={toast.message} />}
        </>
    );
};

export default Search;