// helpers put these above createNotionEntry
const isFileLike = (obj: any): obj is File | Blob => {
    return (!!obj && (obj instanceof File || obj instanceof Blob));
};

const base64ToBlob = (base64: string, contentType = "application/octet-stream") => {
    // handle data URLs
    const match = base64.match(/^data:(.+);base64,(.*)$/);
    let rawBase64 = base64;
    if (match) {
        contentType = match[1];
        rawBase64 = match[2];
    };

    // decode
    const binary = atob(rawBase64 || "");
    const len = binary.length;
    const bytes = new Uint8Array(len);
    for (let i = 0; i < len; i++) bytes[i] = binary.charCodeAt(i);
    return new Blob([bytes], { type: contentType });
};

const urlToFile = async (url: string, filename = "attachment") => {
    const resp = await fetch(url);
    const blob = await resp.blob();
    return new File([blob], filename, { type: blob.type || "application/octet-stream" });
};

export const createNotionEntry = async (
    fileName: string,
    fileUrls: Array<File | Blob | { base64?: string; contentType?: string; name?: string } | string> = [],
    date: string,
    link: string,
    linkedCase: string,
    messageId: string,
    mailBody: string,
    callback: (error: Error | null, data: any | null) => void
): Promise<void> => {
    const formData = new FormData();

    if (fileName != null) formData.append("fileName", fileName);
    if (mailBody != null) formData.append("emailBody", mailBody);
    if (date != null) formData.append("date", date);
    if (link != null) formData.append("link", link);
    if (linkedCase != null) formData.append("linkedCase", linkedCase);
    if (messageId != null) formData.append("messageId", messageId);

    try {
        for (const item of fileUrls) {
            if (!item) {
                console.warn("Skipping empty attachment item:", item);
                continue;
            };

            // If already a File or Blob append directly
            if (isFileLike(item)) {
                const fileToAppend = item instanceof File ? item : new File([item], "attachment", { type: item.type || "application/octet-stream" });
                formData.append("files", fileToAppend);
                continue;
            };

            // If it's a string
            if (typeof item === "string") {
                if (item.startsWith("data:")) {
                    const blob = base64ToBlob(item);
                    const file = new File([blob], "attachment", { type: blob.type || "application/octet-stream" });
                    formData.append("files", file);
                    continue;
                }

                try {
                    const fetchedFile = await urlToFile(item, "attachment");
                    formData.append("files", fetchedFile);
                    continue;
                } catch (err) {
                    console.warn("Failed to fetch attachment URL, skipping:", item, err);
                    continue;
                };
            };

            if (typeof item === "object") {
                const maybe = item as any;
                if (maybe.base64) {
                    const blob = base64ToBlob(maybe.base64, maybe.contentType || "application/octet-stream");
                    const filename = maybe.name || "attachment";
                    const file = new File([blob], filename, { type: blob.type || maybe.contentType || "application/octet-stream" });
                    formData.append("files", file);
                    continue;
                };

                if (maybe.url) {
                    try {
                        const fetchedFile = await urlToFile(maybe.url, maybe.name || "attachment");
                        formData.append("files", fetchedFile);
                        continue;
                    } catch (err) {
                        console.warn("Failed to fetch attachment object.url, skipping:", maybe.url, err);
                        continue;
                    };
                };
            };

            // If we reach here, unknown type skip and warn
            console.warn("Skipping unsupported attachment type:", item);
        };

        // Send to backend
        const response = await fetch("https://optimo-backend.vercel.app/add-task", {
            method: "POST",
            body: formData,
        });

        const data = await response.json();
        if (data.success !== false) {
            callback(null, data);
        } else {
            callback(new Error(data.message), null);
        };
    } catch (error) {
        console.error("Error creating Notion page:", error);
        callback(error as Error, null);
    };
};